@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
table {
  background-color: #f5f5f5;
  border-collapse: collapse;
  margin: 5px;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

td {
  border: 1px solid rgb(199, 199, 199);
  padding: 0.1rem 0.5rem;
  cursor: pointer;
}

:root {
  --dark: #222831;
  --dark-light: #3b5360;
}

* {
  margin: auto;
  padding: auto;
}

body {
  background-color: #eeeeee;
  font-family: Lato, Arial, Helvetica, sans-serif;
  display: flex;
}

.header-container {
  color: white;
  background-color: #222831;
  background-color: var(--dark);
}

h1,
h5 {
  text-align: center;
  margin: 10px 0;
}

h1 {
  margin-top: 0;
  padding: 20px 0;
  cursor: default;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  transition: all 0.2s;
}

.toolbar {
  display: flexbox;
  margin-bottom: 15px;
}

button {
  font-family: Lato, Arial, Helvetica, sans-serif;
  background-color: #222831;
  background-color: var(--dark);
  color: white;
  border: none;
  padding: 10px 15px;
  cursor: pointer;
  outline: none;
}
button:hover {
  transition: all 0.5s;
  background-color: #3b5360;
  background-color: var(--dark-light);
  color: #16c79a;
}

.red {
  color: #f05454;
}

.separated-columns {
  display: flex;
}

.table {
  display: inline;
  margin-left: 4rem;
}

.sketcher-picker {
  float: left;
}

@media all and (max-width: 850px) {
  .separated-columns {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
  }

  .table {
    display: block;
    margin: 0;
    margin-bottom: 25px;
  }

  .sketcher-picker {
    float: none;
  }
}

